$(".catalog-order__show").click(function () {
    let parent = $(this).parents(".catalog-order")
    if($(parent).hasClass("_active")){
        $(".catalog-order").removeClass("_active")
        $(".catalog-order__hidden").slideUp()
    } else{
        $(".catalog-order").removeClass("_active")
        $(".catalog-order__hidden").slideUp()
        $(parent).addClass("_active")
        $(this).siblings(".catalog-order__hidden").slideDown()
    }

})

$(".filter-group__item-btn").click(function () {
    let parent = $(this).parents(".filter-group__item")
    $(parent).toggleClass("_active")
    $(parent).find(".filter-group__item-hidden").slideToggle()
})

$(".filter-group__show").click(function () {
    let parent = $(this).parents(".filter-group")
    $(parent).toggleClass("_active")
    $(parent).find(".filter-group__hidden").slideToggle()
})

$(".filter-btn").click(function () {
    $(".catalog-sidebar").addClass("_active")
    $("body").addClass("fixed-body")
})

$(".catalog-sidebar__close").click(function () {
    $(".catalog-sidebar").removeClass("_active")
    $("body").removeClass("fixed-body")
})

$(".catalog-group__color").click(function () {
    let tabId = $(this).attr("data-tab")
    let parent = $(this).parents(".catalog-group")

    $(parent).find(".catalog-group__color").removeClass("_active")
    $(this).addClass("_active")

    $(parent).find("tr").removeClass("_active")
    $(parent).find(`tr[data-tab="${tabId}"]`).addClass("_active")
})



